<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                getDateList: [],
                getDataList: [],
                getOrderCostList: [],
                getShipList: [],
                siteList: null,
                departmentList: [],
                activeName: 'personal',
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                promotionList: [],
                statistical: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                title: '编辑费用',
                form: {
                    costType: '',
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    site: ''
                },
                totalCosts: '',
                orderSale: '',
                pcts: ''
            }
        },
        methods: {
            updateSite() {
                this.getTotal();
                this.getTotalCost()
            },
            async getTotalCost() {
                this.statistical = [];
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                await this.$post('getTotalFee', {
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    web_id: this.form.site,
                    type: 1
                }).then(res => {
                    this.statistical.push({
                        value: res.order_sales,
                        name: '整站销售额',
                    });
                });
                await this.$post('getTotalFee', {
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    type: 2
                }).then(res => {
                    this.userTotal = this.statistical.filter(item => {
                        return item.name = '整站销售额';
                    });
                    this.statistical.push({
                        value: Number(this.userTotal[0].value) / res.order_users,
                        name: '整站客单价',
                    });
                });
                await this.$post('getTotalFee', {
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    type: 3
                }).then(res => {
                    this.statistical.push({
                        value: res.total_cost,
                        name: '整站花费'
                    });
                });
                if (this.statistical) {
                    this.drawLine3()
                }
            },
            getTotal() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getTotalFeeData', {
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    web_id: this.form.site
                }).then(res => {
                    this.getDateList = res.material.map(item => {
                        return item.date
                    });
                    this.getDataList = res.material.map(item => {
                        return item.total_price
                    });
                    this.getOrderCostList = res.order_cost.map(item => {
                        return item.total_price
                    });
                    this.getShipList = res.ship_fee.map(item => {
                        return item.total_price
                    });
                    if (res.material.length) {
                        this.drawLine();
                    }
                })
            },
            // 站点列表
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: 10000,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    tooltip : {
                        trigger: 'axis',
                            axisPointer: {
                            type: 'cross',
                                label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    legend: {
                        data:['物流费用','物料费用','采购费用']
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    grid: {
                        left: '3%',
                            right: '4%',
                            bottom: '3%',
                            containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : this.getDateList
                        }
                    ],
                        yAxis : [
                    {
                        type : 'value'
                    }
                ],
                    series : [
                    {
                        name:'物流费用',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data: this.getShipList
                    },
                    {
                        name:'物料费用',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data: this.getDataList
                    },
                    {
                        name:'采购费用',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data: this.getOrderCostList
                    }
                ]
                });
            },
            drawLine3() {
                this.names = this.statistical.map(item => {
                    return item.name
                });
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart3'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data: this.names
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data: this.statistical
                        }
                    ]
                });
            },
            // 部门列表
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
        },
        mounted() {
            this.getTotalCost();
            this.getTotal();
            this.getSiteList();
            this.getDepartmentList();
        }
    }
</script>

